<template>
  <section class="intro">
    <header-menu />
    <div class="intro__main">
      <img
        src="../assets/images/logo_nutritionist.svg"
        class="intro_logo">
      <header class="intro__title">Программа коррекции тела</header>
      <a
        class="intro__link"
        href="#program"
        v-scroll-to="'#price'">Начать</a>
    </div>
