</template>

<script>
import HeaderMenu from './HeaderMenu';

export default {
  components: {
    HeaderMenu,
  },
};
</script>

<style>
.intro {
  min-height: 100vh;
  background-image: url(../assets/images/main-backgroung.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  filter: brightness(90%);
}

.intro__main {
  display: flex;
  flex-direction: column;
  flex-direction: column;
  align-items: center;
  flex: 1;
  /* justify-content: center; */
}

/* .intro::after {
  content: '';
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
} */

.intro_logo {
  width: 150px;
  filter: contrast(115%) brightness(105%);
}

.intro__title {
  display: flex;
  text-align: center;
  font-size: 4em;
  color: white;
  font-weight: 600;
  line-height: 1.2;
}

.intro__link {
  font-size: 1.5em;
  margin: 20px;
  padding: 0.4em 1.3em;
  border-radius: 3em;
  border: 0.2em solid var(--p-color-dark);
  background-color: var(--p-color-dark);
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  color: white;
  transition: all 0.2s ease-in-out;
}

.intro__link:hover {
  background-color: white;
  color: var(--p-color-dark);
}
</style>



